    @import "../../scss/abstracts";
    .projects {
        position: relative;
        z-index: 1000;
        margin: 0 auto;
        padding: 10vh 5vw;
        counter-reset: project;
        @include respond(larger-screen) {
            padding: 4vw;
        }
        @include respond(mid-screen) {
            padding: 1vw !important;
            margin-top: 6vh;
        }
    }